*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body{
  background-color: #1b1b32;
}
.App{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato, sans-serif;
}
.logo-container{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 20px;
}
.img-logo{
  width: auto;
  height: 100px;
}

.main-container{
  display: flex;
  flex-wrap: wrap;
 /* flex-direction: column; Este es para alinear en columnas */
  height: auto;
  width: 550px;
  align-items: center;
  justify-content: center;
  
}